import React from 'react';
import Section from "../../Atomics/Section";
import {useUserInfo} from "../../Providers/FireBaseProvider";
import List from "../../Atomics/List";
import ListItem from "../../Atomics/ListItem";
import Icon from "../../Atomics/Icon";

const Profile = () => {
    const userInfo = useUserInfo();

    return (
        <Section title={'Profil'}>
            <List>
                <ListItem>
                    <Icon style={{marginRight: 8}}>
                        face
                    </Icon>
                    {`${userInfo.surname} ${userInfo.lastname}`}
                </ListItem>
                <ListItem>
                    <Icon style={{marginRight: 8}}>
                        email
                    </Icon>
                    {`${userInfo.email}`}
                </ListItem>
            </List>
        </Section>
    );
};

export default Profile;